import { D_CLEAR_FORM_DATA, D_SET_FORM_DATA } from "../types";

const initialState = {}

export const dynamicFormReducer = (state = initialState, action: { type: string, payload: any }) => {
    switch (action.type) {
        case D_SET_FORM_DATA:
            return { ...state, ...action.payload }

        case D_CLEAR_FORM_DATA:
            return { ...initialState }
        default:
            return state;
    }
}